import React, { useEffect, lazy, Suspense } from "react"
import { Routes, Route, Navigate, useLocation } from "react-router-dom"
import ProtectedRoute from '../Context/ProtectedRoute'
import LoadingPage from "../Components/LazyLoadingPage/LoadingPage"
import DashboardLayout from "../Layout/DashboardLayout"

const Dashboard = lazy(() => import("../Pages/Dashboard/Dashboard"))
const ViewProducts = lazy(() => import("../Pages/Products/viewProduct"))
const Services = lazy(() => import("../Pages/ViewServices/viewServices"))
const Favorites = lazy(() => import("../Pages/Favorites/Favorites"))
const Cart = lazy(() => import("../Pages/Cart/Cart"))
const UserSettings = lazy(() => import("../Pages/Config/Config"))
const PurchaseHistory = lazy(() => import("../Pages/History/History"))
const Company = lazy(() => import("../Pages/Company/Company"))
const Buys = lazy(() => import("../Pages/Buys/Buys"))
const MyServices = lazy(() => import("../Pages/MyServices/MyServices"))

const ScrollToTop = () => {
  const { pathname } = useLocation() 
  useEffect(() => { window.scrollTo({ top: 0, behavior: "smooth",}) }, [pathname])
  return null
}

const DashboardRouter = () => {
  return (
    <>
      <ScrollToTop />
      <Suspense fallback={<LoadingPage />}>
        <Routes>
          <Route path="*" element={<Navigate to="/home" />} />
          <Route path="/" element={<DashboardLayout />}>
            <Route path="/" element={<Dashboard />} />
            <Route path="/products" element={<ViewProducts />} />
            <Route path="/products/brand/:brand" element={<ViewProducts />} />
            <Route path="/products/category/:category" element={<ViewProducts />} />
            <Route path="/services" element={<Services />} />
            <Route path="/myservices" element={<MyServices />} />
            <Route path="/favorites" element={<Favorites />} />
            <Route path="/buys/:id" element={<Buys />} />
            <Route path="/cart" element={<Cart />} />
            <Route path="/history" element={<PurchaseHistory />} />
            <Route path="/settings" element={<UserSettings />} />
            <Route path="/company" element={<Company />} />
          </Route>
        </Routes>
      </Suspense>
    </>
  )
}

export default DashboardRouter