import React from "react"
import { Box } from "@mui/material"
import { motion } from "framer-motion"

const dotTransition = {
  duration: 0.6,
  repeat: Infinity,
  ease: "easeInOut",
}

const LoadingPage = () => {
  return (
    <Box
      sx={{
        position: "fixed",
        top: 0,
        left: 0,
        zIndex: 1300,
        width: "100vw",
        height: "100vh",
        backgroundColor: "rgba(255,255,255,0.95)",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        gap: 1,
      }}
    >
      {[0, 1, 2].map((i) => (
        <motion.div
          key={i}
          style={{
            width: 10,
            height: 10,
            borderRadius: "50%",
            backgroundColor: "#1976d2",
          }}
          animate={{ y: ["0%", "-50%", "0%"] }}
          transition={{ ...dotTransition, delay: i * 0.2 }}
        />
      ))}
    </Box>
  )
}

export default LoadingPage