import React from "react"
import { Box, Alert, AlertTitle } from "@mui/material"

const Alerts = ({ prop, closeFunction, params }) => {
    debugger
    return (
        <Box position="fixed" top={16} right={16} zIndex={9999}>
          <Alert severity={prop.severity} onClose={() => closeFunction(params)}>
            <AlertTitle>{prop.title}</AlertTitle>
            <pre style={{ margin: 0 }}>{prop.alert}</pre>
          </Alert>
        </Box>
    )
}

export default Alerts