import React from 'react'
import { AppBar, Toolbar, IconButton, Typography, Menu, MenuItem, Box, Avatar, Drawer, List, ListItem, ListItemIcon, ListItemText, Divider, Grid } from '@mui/material'
import { Computer, ShoppingCart, HeadsetMic, ContactSupport, Search, Inventory, Settings, Logout, Favorite, History, Business, Notifications, Close, AssignmentInd, FactCheck  } from '@mui/icons-material'
import HomeIcon from '@mui/icons-material/Home'
import MenuIcon from '@mui/icons-material/Menu'
import { Link } from 'react-router-dom'
import Badge from '@mui/material/Badge'
import useNavBar from './useNavBar'
import SearchBar from '../Search/Search'
import '../../Resources/Css/Dashboard.css'

const NavBar = () => {
  const { state, handlers } = useNavBar()
  
  return (
    <>
      <AppBar position="sticky" className="gamer-navbar">
        <Toolbar>
          <Grid container display="flex" justifyContent="center">            
            <Grid item xs={2} display="flex" justifyContent="center" sx={{ display: { xs: "flex", lg: "none" }}}>
              <Box alignContent="center">
                <IconButton color="inherit" onClick={handlers.toggleDrawer(true)}>
                  <MenuIcon sx={{ color: 'white' }}/>
                </IconButton>
              </Box>                       
            </Grid>

            <Grid item lg={2} display="flex" justifyContent="center" sx={{ display: { xs: "none", lg: "flex" } }}>
              <Box component="img" src="/img/LogoGlobalblanco.png" sx={{ width: 120, height: 50 }} />
            </Grid> 

            <Grid item lg={2} display="flex" justifyContent="center" alignItems="center" sx={{ display: { xs: "none", lg: "flex" } }}>                       
              <SearchBar />
            </Grid>

            <Grid item xs={7} display="flex" justifyContent="center" sx={{ display: { xs: "flex", lg: "none" } }}>
              <Box component="img" src="/img/LogoGlobalblanco.png" sx={{ width: 110, height: 40 }} />
            </Grid> 

            <Grid item lg={6} sx={{ display: { xs: "none", sm: "none", md: "none", lg: "flex" } }} justifyContent="center">
              <Box className="menu-horizontal">
                <Typography variant="button" component={Link} to="/home"
                  className={`menu-link ${ state.location === "/home" ? "menu-active" : "" }`}>
                  <Computer className={`menu-icon ${ state.location === "/home" ? "icon-active" : "" }`} /> Inicio
                </Typography>
                <Typography variant="button" component={Link} to="/home/products"
                  className={`menu-link ${ state.location === "/home/products" ? "menu-active" : "" }`}>
                  <Inventory className={`menu-icon ${ state.location === "/home/products" ? "menu-active" : "" }`}/> Productos
                </Typography>
                <Typography variant="button" component={Link} to="/home/services"
                  className={`menu-link ${ state.location === "/home/services" ? "menu-active" : "" }`}>
                  <HeadsetMic className={`menu-icon ${ state.location === "/home/services" ? "menu-active" : "" }`} /> Servicios
                </Typography>
                <Typography variant="button" component={Link} className="menu-link" onClick={handlers.scrollToFooter}>
                  <ContactSupport className="menu-icon" /> Contacto
                </Typography>
              </Box>
            </Grid>

            <Grid item xs={3} lg={2} display="flex" justifyContent="center" alignItems="center">            
              <Box display="flex">  
                <Box className="cart-icon" sx={{ display: { xs: "none", lg: "flex" } }}>
                  <IconButton component={Link} to="/home/favorites">
                    <Badge badgeContent={state.favoritesCount} color="secondary">
                      <Favorite sx={{ color: state.location === '/home/favorites' ? '#e7cda8' : 'white' }} />
                    </Badge>
                  </IconButton>
                </Box>

                {/* Ícono del carrito con contador */}
                <Box className="cart-icon" sx={{ display: { xs: "none", lg: "flex" } }}>
                  <IconButton component={Link} to="/home/cart">
                    <Badge badgeContent={state.cartCount} color="secondary">
                      <ShoppingCart sx={{ color: state.location === '/home/cart' ? '#e7cda8' : 'white' }} />
                    </Badge>
                  </IconButton>
                </Box>

                {/* Ícono de notificaciones con contador */}
                <Box className="cart-icon">
                  <IconButton onClick={() => handlers.setDrawerNotifyOpen(true)}>
                    <Badge badgeContent={state.notifyCount} color="error">
                      <Notifications sx={{ color: state.location === '/home/notifications' ? '#e7cda8' : 'white' }} />
                    </Badge>
                  </IconButton>
                </Box>

                {/* Drawer para las notificaciones */}
                <Drawer anchor="right" open={state.drawerNotifyOpen} onClose={() => handlers.setDrawerNotifyOpen(false)} sx={{ '& .MuiDrawer-paper': { width: 300 }}}>
                  <Box sx={{ backgroundColor: '#29354c', display: 'flex', justifyContent: 'space-between', alignItems: 'center', p: 2 }}>
                    <Box sx={{ display: 'flex', alignItems: 'center' }}>
                      <Notifications className="menu-icon" sx={{ mr: 1 }} />
                      <Typography variant="h6" className="menu-icon">Notificaciones</Typography>
                    </Box>
                    <IconButton onClick={() => handlers.setDrawerNotifyOpen(false)}>
                      <Close className="menu-icon"/>
                    </IconButton>
                  </Box>

                  <Divider />

                  <List sx={{ backgroundColor: '#29354c', height: '100%' }}>
                    {state.notifications.length > 0 ? (
                      state.notifications.map((notification) => (
                        <ListItem
                          key={notification.id}
                          sx={{ mb: 2,
                            display: 'flex',
                            justifyContent: 'space-between',
                            alignItems: 'center',
                          }}>
                          <Box>
                            <Typography variant="subtitle1" className="menu-icon" fontWeight="bold">
                              {notification.title}
                            </Typography>
                            <Typography variant="body2" className="menu-icon">
                              {notification.description}
                            </Typography>
                          </Box>
                          <IconButton onClick={() => handlers.removeNotification(notification.id)}>
                            <Close fontSize="small" className="menu-icon" />
                          </IconButton>
                        </ListItem>
                      ))
                    ) : (
                      <Typography variant="body2" align="center" sx={{ mt: 2, color: 'text.secondary' }}>
                        No hay notificaciones nuevas.
                      </Typography>
                    )}
                  </List>
                </Drawer>   

                <Box className="user-avatar" px={1}>
                  <IconButton onClick={handlers.handleUserMenuOpen}>
                    <Avatar alt="Usuario" src={state.token?.user?.picture || "/img/avatar.png"} referrerPolicy="no-referrer" />
                  </IconButton>
                  <Menu
                    anchorEl={state.userMenuAnchorEl}
                    open={state.isUserMenuOpen}
                    onClose={handlers.handleUserMenuClose}
                    anchorOrigin={{
                      vertical: "top",
                      horizontal: "right",
                    }}
                    transformOrigin={{
                      vertical: "top",
                      horizontal: "right",
                    }}>
                      
                    <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/home/settings">
                      <Settings style={{ marginRight: "10px" }} />Configuraciones
                    </MenuItem>
                    <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/home/myservices">
                      <FactCheck style={{ marginRight: "10px" }} />Mis servicios
                    </MenuItem>
                    <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/home/history">
                      <History style={{ marginRight: "10px" }} />Historial
                    </MenuItem>
                    <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/home/company">
                      <Business style={{ marginRight: "10px" }} />Nosotros
                    </MenuItem>
                    <MenuItem onClick={handlers.handleUserMenuClose} component={Link} to="/">
                      <Logout style={{ marginRight: "10px" }} />Cerrar Sesión
                    </MenuItem>
                  </Menu>
                </Box>
              </Box>
            </Grid>
          </Grid>                                      
        </Toolbar>
        {/* Línea animada multicolor */}
        <Box className="animated-line" />              
      </AppBar>
      
      <Box className="navBar-floating-container" sx={{ display: { xs: "flex", lg: "none" } }}>
        <Grid container display="flex" justifyContent="center" sx={{ height: '65px' }}>            
            <Grid item xs={12}>
              <Box className="animated-line-footer" />
            </Grid>
            <Grid item xs={3} display="flex" justifyContent="center">
              <Box className="cart-icon" alignContent="center">
                  <IconButton component={Link} to="/home" sx={{ pb: 0 }}>
                    <HomeIcon sx={{ color: state.location === '/home' ? '#e7cda8' : 'white' }} />
                  </IconButton>
                  <Typography variant="body2" align="center" sx={{ color: state.location === '/home' ? '#e7cda8' : 'white' }}>Inicio</Typography>
              </Box>
            </Grid>
            <Grid item xs={3} display="flex" justifyContent="center">
              <Box className="cart-icon" alignContent="center">
                  <IconButton component={Link} to="/home/favorites" sx={{ pb: 0, width: 100 }} justifyContent="center">
                    <Badge badgeContent={state.favoritesCount} color="secondary">
                      <Favorite sx={{ color: state.location === '/home/favorites' ? '#e7cda8' : 'white' }} />
                    </Badge>
                  </IconButton>
                  <Typography variant="body2" align="center" sx={{ color: state.location === '/home/favorites' ? '#e7cda8' : 'white' }}>Favoritos</Typography>
              </Box>
            </Grid>
            <Grid item xs={3} display="flex" justifyContent="center">
              <Box className="cart-icon" alignContent="center">
                <IconButton component={Link} to="/home/cart" sx={{ pb: 0, width: 100 }} justifyContent="center">
                  <Badge badgeContent={state.cartCount} color="secondary">
                    <ShoppingCart sx={{ color: state.location === '/home/cart' ? '#e7cda8' : 'white' }} />
                  </Badge>
                </IconButton>
                <Typography variant="body2" align="center" sx={{ color: state.location === '/home/cart' ? '#e7cda8' : 'white' }}>Carrito</Typography>
              </Box>      
            </Grid>
            <Grid item xs={3} display="flex" justifyContent="center">
              <Box className="cart-icon" alignContent="center">
                <IconButton color="inherit" sx={{ pb: 0, width: 100 }} onClick={handlers.toggleSearchDrawer(true)}>
                  <Search sx={{ color: 'white' }}/>
                </IconButton>
                <Typography variant="body2" align="center" color="white">Buscar</Typography>
              </Box>       
            </Grid>            
        </Grid>

        <Drawer anchor="top" open={state.drawerSearchOpen} onClose={handlers.toggleSearchDrawer(false)} ModalProps={{ keepMounted: true }}
          PaperProps={{
          sx: {
            overflow: "visible",
            height: "auto"
          },
        }}>
          <Box sx={{ backgroundColor: '#29354c', display: 'flex', justifyContent: 'space-between', alignItems: 'center', p: 2 }}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Search className="menu-icon" sx={{ mr: 1 }} />
              <Typography variant="h6" className="menu-icon">Buscador</Typography>
            </Box>
            <IconButton onClick={handlers.toggleSearchDrawer(false)}>
              <Close className="menu-icon"/>
            </IconButton>
          </Box>

          <Divider />

          <Box sx={{ height: 100, width: '100%', backgroundColor: '#29354c', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <SearchBar onCloseDrawer={handlers.toggleSearchDrawer(false)} />
          </Box>
        </Drawer>    

        <Drawer anchor="left" open={state.drawerOpen} onClose={handlers.toggleDrawer(false)}>
          <Box sx={{ backgroundColor: '#29354c', display: 'flex', justifyContent: 'space-between', alignItems: 'center', p: 2 }}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <HomeIcon className="menu-icon" sx={{ mr: 1 }} />
              <Typography variant="h6" className="menu-icon">Menu</Typography>
            </Box>
            <IconButton onClick={handlers.toggleDrawer(false)}>
              <Close className="menu-icon" />
            </IconButton>
          </Box>

          <Divider />

          <Box sx={{ height: '100%', backgroundColor: '#29354c' }}>
            <Box sx={{ width: 250 }} role="presentation" onClick={handlers.toggleDrawer(false)} onKeyDown={handlers.toggleDrawer(false)}>              
              <List>
                <ListItem button component={Link} to="/home" className={`menu-link ${state.location === "/home" ? "menu-active" : ""}`}>
                  <ListItemIcon>
                    <Computer className={`menu-icon ${state.location === "/home" ? "menu-active" : ""}`}/>
                  </ListItemIcon>
                  <ListItemText primary="Inicio"  className={`menu-icon ${state.location === "/home" ? "menu-active" : ""}`} />
                </ListItem>
                <ListItem button component={Link} to="/home/products" className={`menu-link ${state.location === "/home/products" ? "menu-active" : ""}`}>
                  <ListItemIcon>
                    <Inventory className={`menu-icon ${state.location === "/home/products" ? "menu-active" : ""}`} />
                  </ListItemIcon>
                  <ListItemText primary="Productos" className={`menu-icon ${state.location === "/home/products" ? "menu-active" : ""}`} />
                </ListItem>
                <ListItem button component={Link} to="/home/services" className={`menu-link ${state.location === "/home/services" ? "menu-active" : ""}`}>
                  <ListItemIcon>
                    <HeadsetMic className={`menu-icon ${state.location === "/home/services" ? "menu-active" : ""}`} />
                  </ListItemIcon>
                  <ListItemText primary="Servicios" className={`menu-icon ${state.location === "/home/services" ? "menu-active" : ""}`} />
                </ListItem>
                <ListItem button component={Link} onClick={handlers.scrollToFooter} className={`menu-link`}>
                  <ListItemIcon>
                    <ContactSupport className={`menu-icon`} />
                  </ListItemIcon>
                  <ListItemText primary="Contacto" className={`menu-icon`} />
                </ListItem>
              </List>
            </Box>
          </Box>
        </Drawer>     
      </Box>
    </>
  )
}

export default NavBar
