import { useState } from "react"
import helper from '../../Resources/Class/helper'
import server from '../../Resources/Json/Endpoints.json'
import utils from "../../Resources/Class/utils"

const useWhatsapp = () => {
    const [isOpen, setIsOpen] = useState(false)
    const [isLoading, setIsLoading] = useState(false)
    const [success, setSuccess] = useState(null)
    const [error, setError] = useState(null)
    const [formData, setFormData] = useState({
        phoneNumber: "+5491126960767",
        message: "",
    })
    const [isPhoneComplete, setIsPhoneComplete] = useState(false)

    const handleToggle = () => {
        setIsOpen(!isOpen)
    }

    const handlePhoneChange = (e) => {
        const value = e.target.value
        setFormData({ ...formData, phoneNumber: value })
        setIsPhoneComplete(value.trim().length === 17)
    }

    const handleInputChange = (e) => {
        const { name, value } = e.target
        setFormData({ ...formData, [name]: value })
    }

    const handleSubmit = () => {
        const sanitizedPhone = formData.phoneNumber.replace(/[^0-9]/g, "")
        const whatsappLink = `https://wa.me/${sanitizedPhone}?text=${encodeURIComponent(formData.message)}`
        window.open(whatsappLink, "_blank")
    }

    // const handleSubmit = event => {
    //     event.preventDefault()
    //     setIsLoading(true)
    //     helper.axiosRequest({
    //         method: "POST",
    //         url: server.endpoints.sendWhatsapp,
    //         data: { to: `whatsapp:${formData.phoneNumber}`.replace(/\s+/g, ""), body: formData.message }
    //     })
    //     .then(data => { utils.success(data, setSuccess) })
    //     .catch(error => { utils.error(error, setError) })
    //     .finally(() => {
    //         setIsLoading(false)
    //         setIsOpen(false)
    //     })
    // }

    const state = {
        isOpen,
        formData,
        isPhoneComplete,
        success,
        error,
        isLoading,
        setSuccess,
        setError,
        setIsLoading
    }
    
    const handlers = {
        handleToggle,
        handlePhoneChange,
        handleInputChange,
        handleSubmit
    }
    
    return { state, handlers }
}

export default useWhatsapp