import React from "react"
import { Box, Typography, CircularProgress } from '@mui/material'
import '../../Resources/Css/LoadingPage.css'

const loadingPage = () => {
    return (
        <Box className="loading-overlay">
            {/* Animación del loader */}
            <CircularProgress
                size={80}
                thickness={4}
                sx={{
                    color: 'primary.main',
                    animation: 'spin 2s linear infinite',
                    mb: 2,
                }}
            />
   
            {/* Texto animado */}
            <Typography
                variant="h6"
                color="white"
                sx={{ animation: 'fade 1.5s ease-in-out infinite' }}>
                Cargando...
            </Typography>
       </Box>
    )
}

export default loadingPage