import React from 'react'
import {Box, Paper, Typography, IconButton, TextField, InputAdornment, Slide, List, ListItem, ListItemText} from '@mui/material'
import SendIcon from '@mui/icons-material/Send'
import SmartToyIcon from '@mui/icons-material/SmartToy'
import CloseIcon from '@mui/icons-material/Close'
import UseChatGpt from './UseChatGpt'

const ChatGpt = () => {
  const { state, handlers } = UseChatGpt()

  return (
    <Box sx={{ position: 'fixed', bottom: { xs: 80, lg: 16 }, left: 16, zIndex: 1000 }}>
      {!state.open && (
        <Box display="flex" alignItems="center" gap={1}>
          <IconButton
            onClick={() => handlers.setOpen(true)}
            sx={{
              bgcolor: '#10a37f', color: 'white',
              '&:hover': { bgcolor: '#0e8e6d' }
            }}
          >
            <SmartToyIcon />
          </IconButton>
          <Paper elevation={3} sx={{ padding: '4px 8px', borderRadius: 2 }}>
            <Typography variant="subtitle2" fontWeight="bold">Asistente IA</Typography>
          </Paper>
        </Box>
      )}

      <Slide direction="up" in={state.open} mountOnEnter unmountOnExit>
        <Paper elevation={4} sx={{ width: 360, height: 600, borderRadius: 2, display: 'flex', flexDirection: 'column' }}>
          {/* Header */}
          <Box display="flex" justifyContent="space-between" alignItems="center" p={2} borderBottom="1px solid #ddd">
            <Typography variant="subtitle1" fontWeight="bold" color="#10a37f">Asistente IA</Typography>
            <IconButton onClick={() => handlers.setOpen(false)} size="small">
              <CloseIcon />
            </IconButton>
          </Box>

          {/* Chat body */}
          <Box flex={1} overflow="auto" p={2}>
            <List>
              {state.messages.map((msg, index) => (
                <ListItem key={index} sx={{ justifyContent: msg.sender === 'user' ? 'flex-end' : 'flex-start' }}>
                  <ListItemText
                    primary={msg.text}
                    sx={{
                      bgcolor: msg.sender === 'user' ? '#3b90b5' : '#f1f1f1',
                      color: msg.sender === 'user' ? 'white' : 'black',
                      px: 2,
                      py: 1,
                      borderRadius: 2,
                      maxWidth: '80%',
                      textAlign: msg.sender === 'user' ? 'right' : 'left',
                    }}
                  />
                </ListItem>
              ))}
            </List>
          </Box>

          {/* Input area */}
          <Box p={2} borderTop="1px solid #ddd">
            <TextField
              fullWidth
              placeholder="Escribí tu consulta..."
              variant="outlined"
              value={state.message}
              onChange={(e) => handlers.setMessage(e.target.value)}
              onKeyPress={(e) => {
                if (e.key === 'Enter') {
                  e.preventDefault()
                  handlers.handleSend()
                }
              }}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton onClick={handlers.handleSend} sx={{ color: '#10a37f' }}>
                      <SendIcon />
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </Box>
        </Paper>
      </Slide>
    </Box>
  )
}

export default ChatGpt