import language from '../Json/Languages/Es.json'

class Utils{
    async successLogin(data, callback, navigate){
        await callback(data)
        navigate("/home")
    }

    successCreateUser(data, setAlert, setShowPassword, setFormData){
        setAlert(data?.message)
        setShowPassword(false)
        setFormData(prevData => ({
            ...prevData,
            isFlipped: false,
            name: "",
            lastName: "",
            username: "",
            password: "",
            passwordConfirm: "",
            email: "",
            gender: "",
            errors: {},
        }))
        setTimeout(() => {
            setAlert(null)
        }, 5000)
    }

    success(data, callback){
        callback(data?.message)
        setTimeout(() => {
            callback(null)
        }, 5000)
    }

    error(error, callback){
        callback(error.response?.data?.message || language.lblLogin)
        setTimeout(() => {
            callback(null)
        }, 5000)
    }
}

const utils = new Utils()
export default utils