import useLogin from "./useLogin"
import { Box, Button, TextField, Typography, Paper
, Link, IconButton, InputAdornment
, FormControl, InputLabel, Select, MenuItem } from '@mui/material'
import { Visibility, VisibilityOff, Google } from '@mui/icons-material'
import Alerts from "../../Components/Alerts/Alert"
import LoadingPage from '../../Components/LoadingPage/LoadingPage'
import '../../Resources/Css/Form.css'

const LoginForm = () => {
  const { state, handlers, breakpoints } = useLogin()

  return (
    <Box display="flex" justifyContent="center" alignItems="center" sx={{ minHeight: "100dvh" }}>
      {/* Pantalla de carga */}
      {state.isLoading && ( <LoadingPage /> )}

      {/* Video de fondo */}
      <Box className="video-container">
        {breakpoints.isMobile ? (
          <Box
            component="img"
            src="/img/background.jpg"
            alt="Background"
            className="background-image"
            sx={{
              width: "100%",
              height: "100%",
              objectFit: "cover",
              position: "absolute",
              top: 0,
              left: 0,
              zIndex: -1,
            }}
          />
        ) : (
          <Box className="video-container">
            <Box
              component="video"
              autoPlay
              muted
              loop
              className="background-video">
              <source src="/img/manos.mp4" type="video/mp4" />
            </Box>
          </Box>
        )}
      </Box>

      {/* Alert para errores */}
      {state.success && (
        <Alerts 
          prop={{severity: "success", title: "Completado", alert: state.success}}  
          closeFunction={state.setSuccess} 
          params={null}
        /> 
      )}
      {state.warning && (
        <Alerts 
          prop={{severity: "warning", title: "Error", alert: state.warning}}  
          closeFunction={state.setWarning} 
          params={null}
        /> 
      )}
      {state.error && (
        <Alerts 
          prop={{severity: "error", title: "Error", alert: state.error}}  
          closeFunction={state.setError} 
          params={null}
        /> 
      )}

      <Box className={`flip-card ${state.formData.isFlipped ? 'flipped' : ''}`}>
        <Box className="flip-card-inner">
          {/* Formulario de inicio de sesión (frente) */}
          <Paper className="flip-card-front" elevation={3} style={{ width: '100%', backgroundColor: 'rgba(255, 255, 255, 0.8)', backdropFilter: 'blur(10px)', borderRadius: '10px' }}>
            <Typography variant="h5" align="center" gutterBottom>Inicio de Sesión</Typography>
            <form onSubmit={handlers.handleSubmit} onKeyDown={event => handlers.handleKeyDown(event, handlers.handleSubmit)} key={state.formData.isFlipped ? "register" : "login"}>
              <TextField
                id={`login-username`}
                label="Usuario o Email"
                variant="outlined"
                fullWidth
                margin="normal"
                name="username"
                value={state.formData.username}
                onChange={handlers.handleInputChange}
                error={!!state.formData.errors.username}
              />
              <TextField
                id={`login-password`}
                label="Contraseña"
                variant="outlined"
                fullWidth
                margin="normal"
                type={state.showPassword ? 'text' : 'password'}
                name="password"
                value={state.formData.password}
                onChange={handlers.handleInputChange}
                error={!!state.formData.errors.password}
                slotProps={{
                  input: {
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handlers.handleTogglePasswordVisibility}
                          edge="end"
                        >
                          {state.showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }
                }}
              />
              <Box display="flex" justifyContent="center" marginTop="1rem">
                <Link component="button" variant="body2" onClick={handlers.handleFlip}>¿No tienes cuenta? Crear una cuenta</Link>
              </Box>
              <Box display="flex" justifyContent="center" marginTop="1rem">
                <Link component="button" variant="body2" onClick={handlers.handleFlip}>¿Olvidaste tu contraseña?</Link>
              </Box>
              <Button type="submit" variant="contained" color="primary" fullWidth style={{ marginTop: '1rem' }}>Iniciar Sesión</Button>
              <Button onClick={handlers.handleGoogle} variant="contained" className="google-button" fullWidth startIcon={<Google />}>Iniciar Sesión con Google</Button>
            </form>
          </Paper>

          {/* Formulario de registro (reverso) */}
          <Paper className="flip-card-back" elevation={3} style={{ width: '100%', backgroundColor: 'rgba(255, 255, 255, 0.8)', backdropFilter: 'blur(10px)', borderRadius: '10px' }}>
            <Typography variant="h5" align="center" gutterBottom>Crear Cuenta</Typography>
            <form onSubmit={handlers.handleSubmitRegister} onKeyDown={event => handlers.handleKeyDown(event, handlers.handleSubmitRegister)} key={state.formData.isFlipped ? "register" : "login"}>
              <Box
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  gap: '16px',
                  width: '100%',
                }}>
                <TextField
                  id={`name`}
                  label="Nombre"
                  variant="outlined"
                  name="name"
                  margin="normal"
                  value={state.formData.name}
                  error={!!state.formData.errors.name}
                  onChange={handlers.handleInputChange}
                  style={{ flex: 1 }}/>
                <TextField
                  id={`lastName`}
                  label="Apellido"
                  variant="outlined"
                  name="lastName"
                  margin="normal"
                  value={state.formData.lastName}
                  error={!!state.formData.errors.lastName}
                  onChange={handlers.handleInputChange}
                  style={{ flex: 1 }}/>
              </Box>
              <Box
                style={{
                  display: 'flex',
                  justifyContent: 'space-between',
                  gap: '16px',
                  width: '100%',
                }}>
                  <TextField
                    id={`register-username`}
                    label="Nombre de Usuario"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                    name="username"
                    value={state.formData.username}
                    onChange={handlers.handleInputChange}
                    error={!!state.formData.errors.username}
                  />
                  <FormControl fullWidth margin="normal" variant="outlined">
                    <InputLabel id="gender-label" error={!!state.formData.errors.gender}>Género</InputLabel>
                    <Select
                      id={`gender`}
                      name="gender"
                      label="Género"
                      value={state.formData.gender}
                      onChange={handlers.handleInputChange}
                      error={!!state.formData.errors.gender}
                    >
                      <MenuItem value="M">Masculino</MenuItem>
                      <MenuItem value="F">Femenimo</MenuItem>
                    </Select>
                  </FormControl>
              </Box>
              <TextField
                id={`register-email`}
                label="Email"
                variant="outlined"
                fullWidth
                margin="normal"
                name="email"
                value={state.formData.email}
                onChange={handlers.handleInputChange}
                error={!!state.formData.errors.email}
              />
              <TextField
                id={`register-password`}
                label="Contraseña"
                variant="outlined"
                fullWidth
                margin="normal"
                type={state.showPassword ? 'text' : 'password'}
                name="password"
                value={state.formData.password}
                onChange={handlers.handleInputChange}
                error={!!state.formData.errors.password}
                slotProps={{
                  input: {
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handlers.handleTogglePasswordVisibility}
                          edge="end"
                        >
                          {state.showPassword ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }
                }}
              />
              <TextField
                id={`register-password-confirm`}
                label="Confirmar contraseña"
                variant="outlined"
                fullWidth
                margin="normal"
                type={state.showPasswordConfirm ? 'text' : 'password'}
                name="passwordConfirm"
                value={state.formData.passwordConfirm}
                onChange={handlers.handleInputChange}
                error={!!state.formData.errors.passwordConfirm}
                slotProps={{
                  input: {
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton
                          aria-label="toggle password visibility"
                          onClick={handlers.handleTogglePasswordVisibilityConfirm}
                          edge="end"
                        >
                          {state.showPasswordConfirm ? <VisibilityOff /> : <Visibility />}
                        </IconButton>
                      </InputAdornment>
                    )
                  }
                }}
              />
              <Box display="flex" justifyContent="center" marginTop="1rem">
                <Link component="button" variant="body2" onClick={handlers.handleFlip}>¿Ya tienes una cuenta? Inicia Sesión</Link>
              </Box>
              <Button type="submit" variant="contained" color="primary" fullWidth style={{ marginTop: '1rem' }}>Registrarse</Button>
            </form>
          </Paper>
        </Box>
      </Box>
    </Box>
  )
}

export default LoginForm
