import { useState, useEffect } from "react"

const UseChatGpt = () => {
    const [open, setOpen] = useState(false)
    const [message, setMessage] = useState('')
    const [messages, setMessages] = useState([])
    const user = { name: 'José' }

    useEffect(() => {
        open && messages.length === 0 && setMessages([
            {
                sender: 'bot',
                text: `¡Bienvenido a GlobalComputer, ${user.name}! ¿En qué puedo ayudarte hoy?`,
            }
        ])
      }, [open, messages.length, user.name])  
    
      const handleSend = () => {
        if (message.trim() !== '') {
          setMessages(prev => [...prev, { sender: 'user', text: message }])
          setMessage('')
        }
      }

    const state = {
        open,
        message,
        messages
    }
    
    const handlers = {
        setOpen,
        setMessage,
        setMessages,
        handleSend
    }
    
    return { state, handlers }
}

export default UseChatGpt