import React, { useState, useMemo } from "react"
import { TextField, List, ListItem, ListItemAvatar, Avatar, ListItemText, Paper, ClickAwayListener, Box } from "@mui/material"
import { useNavigate } from "react-router-dom"
import Fuse from "fuse.js"
import { useUser } from '../../Context/Provider'

const SearchBar = ({ onCloseDrawer }) => {
  const { dataContext } = useUser()
  const [search, setSearch] = useState("")
  const [results, setResults] = useState([])
  const [open, setOpen] = useState(false)
  const navigate = useNavigate()

  const fuse = useMemo(() => {
    return new Fuse(dataContext, {
      keys: ["name", "brand", "category"],
      threshold: 0.3,
    })
  }, [dataContext])

  const handleChange = (e) => {
    const value = e.target.value
    setSearch(value)

    if (value.trim() === "") {
      setResults([])
      setOpen(false)
      return
    }

    const fuseResults = fuse.search(value)
    const mapped = fuseResults.map((r) => r.item)
    setResults(mapped)
    setOpen(true)
  }

  const handleClickAway = () => {
    setOpen(false)
  }

  const handleSelect = (product) => {
    setSearch(product.name)
    setOpen(false)    
    navigate(`/home/buys/${product.id}`)
    onCloseDrawer && onCloseDrawer(false)
  }

  return (
    <ClickAwayListener onClickAway={handleClickAway}>
      <Box style={{ position: "relative", width: "100%", maxWidth: 400 }}>
        <TextField
          fullWidth
          label="Buscar"
          variant="outlined"
          value={search}
          onChange={handleChange}
          onFocus={() => search && results.length > 0 && setOpen(true)}
          InputProps={{
            sx: { height: 30, backgroundColor: 'white' }
          }}
          InputLabelProps={{
            sx: { color: 'black', top: "-6px", "&.Mui-focused": { color: 'white' }, "&.MuiInputLabel-shrink": { color: 'white' } }
          }}
          sx={{ "& .MuiOutlinedInput-root": { height: 40, backgroundColor: 'white', fontSize: '1rem' } }}
        />

        {open && results.length > 0 && (
          <Paper
            style={{
              position: "absolute",
              top: "100%",
              left: 0,
              right: 0,
              zIndex: 10,
              maxHeight: 300,
              overflowY: "auto",
            }}>
            <List>
              {results.map((product) => (
                <ListItem button key={product.id} onClick={() => handleSelect(product)}>
                  <ListItemAvatar><Avatar src={product.image} alt={product.name} /></ListItemAvatar>
                  <ListItemText primary={product.name} secondary={product.category} />
                </ListItem>
              ))}
            </List>
          </Paper>
        )}
      </Box>
    </ClickAwayListener>
  )
}

export default SearchBar