import React from "react"
import { Fab, Box, TextField, Typography, Button } from "@mui/material"
import WhatsAppIcon from "@mui/icons-material/WhatsApp"
import CloseIcon from "@mui/icons-material/Close"
import useWhatsApp from "./useWhatsapp"
import Alerts from "../Alerts/Alert"
import LoadingPage from "../LoadingPage/LoadingPage"
import "../../Resources/Css/Whatsapp.css"

const WhatsApp = () => {
  const { state, handlers } = useWhatsApp()

  return (
    <>
      {/* Pantalla de carga */}
      {state.isLoading && ( <LoadingPage /> )}

      {/* Alert para errores */}
      {state.success && (
        <Alerts 
          prop={{severity: "success", title: "Completado", alert: state.success}}  
          closeFunction={state.setSuccess} 
          params={null}
        /> 
      )}
      {state.error && (
        <Alerts 
          prop={{severity: "error", title: "Error", alert: state.error}}  
          closeFunction={state.setError} 
          params={null}
        /> 
      )}

      {/* Botón flotante */}
      <Box className="floating-container" sx={{ bottom: { xs: 72, lg: 16 } }}>
        {!state.isOpen && <Typography className="floating-text" sx={{ display: { xs: 'none', lg: 'flex' } }}>Chatea con nosotros</Typography>}
        <Fab aria-label="whatsapp" onClick={handlers.handleToggle} className={`whatsapp-button ${state.isOpen ? "open" : ""}`}>
          {state.isOpen ? <CloseIcon /> : <WhatsAppIcon />}
        </Fab>
      </Box>

      {/* Récuadro flotante */}
      {state.isOpen && (
        <Box className="floating-box animated-slide-in" sx={{ bottom: { xs: '135px', lg: '80px' } }}>
          {/* Cabecera verde */}
          <Box className="header-box">
            <WhatsAppIcon className="whatsapp-logo" />
            <Typography variant="h6" className="header-title">
              WhatsApp
            </Typography>
          </Box>

          {/* Input para mensaje */}
          <TextField
            fullWidth
            label="Mensaje"
            variant="outlined"
            multiline
            rows={4}
            name="message"
            value={state.formData.message}
            onChange={handlers.handleInputChange}
            className="input-field"
          />

          {/* Botón de enviar */}
          <Button
            variant="contained"
            fullWidth
            onClick={handlers.handleSubmit}            
            sx={{ backgroundColor: "#25d366", color: "white", fontWeight: "bold", "&:hover": { backgroundColor: "#1da851" } }}>
            Enviar
          </Button>
        </Box>
      )}
    </>
  )
}

export default WhatsApp
