import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { useUser } from "../../Context/Provider"
import language from '../../Resources/Json/Languages/Es.json'
import server from '../../Resources/Json/Endpoints.json'
import helper from '../../Resources/Class/helper'
import utils from '../../Resources/Class/utils'
import { getAuth, GoogleAuthProvider, signInWithPopup } from "firebase/auth"
import { initializeApp } from "firebase/app"
import { useTheme } from '@mui/material/styles'
import { useMediaQuery } from '@mui/material'

const useLogin = () => {
  const navigate = useNavigate()
  const theme = useTheme()
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'))
  const { login } = useUser()
  const [isLoading, setIsLoading] = useState(false)
  const [success, setSuccess] = useState(null)
  const [warning, setWarning] = useState(null)
  const [error, setError] = useState(null)
  const [showPassword, setShowPassword] = useState(false)
  const [showPasswordConfirm, setShowPasswordConfirm] = useState(false)
  const [formData, setFormData] = useState({
    name: "",
    lastName: "",
    username: "",
    password: "",
    passwordConfirm: "",
    email: "",
    gender: "",
    isFlipped: false,
    errors: {},
  })

  const handleInputChange = event => {
    const { name, value } = event.target
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }))
  }

  const validateFields = () => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
    const validationErrors = {
      name: formData.isFlipped && !formData.name ? language.lblName : null,
      lastName: formData.isFlipped && !formData.lastName ? language.lblLastName : null,
      username: !formData.username ? language.lblUsername : null,
      gender: formData.isFlipped && !formData.gender ? language.lblGender : null,
      email: !formData.isFlipped ? null : !formData.email ? language.lblEmail : formData.email && !emailRegex.test(formData.email) ? language.lblRegex : null,
      password: !formData.password ? language.lblPassword : formData.password.length < 6 ? language.lblPasswordQuantity : null,
      passwordConfirm: !formData.isFlipped ? null : formData.password !== formData.passwordConfirm ? language.lblPasswordConfirm : null
    }

    const isAllNull = obj => { return Object.values(obj).every(value => value === null) }
    const message = Object.values(validationErrors)
      .filter(value => value !== null)
      .map(value => `- ${value}`)
      .join('\n')

    const validate = !isAllNull(validationErrors)
    setFormData(prevData => ({
      ...prevData,
      errors: validationErrors,
    }))
    debugger
    setWarning(message)

    validate && setTimeout(() => {
      setWarning(false)
    }, 5000)

    return validate
  }

  const handleSubmit = async event => {
    event.preventDefault()
    if (validateFields()) return

    setIsLoading(true)
    helper.axiosRequest({
      method: "POST",
      url: server.endpoints.userLogin,
      data: { userData: formData.username, password: formData.password }
    })
    .then(data => { utils.successLogin(data, login, navigate)  })
    .catch(error => { utils.error(error, setError) })
    .finally(() => { setIsLoading(false) })
  }

  const handleSubmitRegister = async event => {
    event.preventDefault()
    if (validateFields()) return

    setIsLoading(true)
    helper.axiosRequest({
      method: "POST",
      url: server.endpoints.createUser,
      data: { name: formData.name
        , lastname: formData.lastName
        , username: formData.username
        , password: formData.password 
        , email: formData.email
        , gender: formData.gender
        , telephone: 0
        , to: formData.email
        , subject: '¡Bienvenido!'
      }
    })
    .then(data => { utils.successCreateUser(data, setSuccess, setShowPassword, setFormData) })
    .catch(error => { utils.error(error, setError) })
    .finally(() => { setIsLoading(false) })
  }

  const handleGoogle = async () => {
    const firebaseConfig = {
      apiKey: "AIzaSyB-ctqP5xPEbw6PhbCVPZ6TenFhnPDk-ts",
      authDomain: "globalcomputer-fe5d7.firebaseapp.com",
      projectId: "globalcomputer-fe5d7",
      storageBucket: "globalcomputer-fe5d7.firebasestorage.app",
      messagingSenderId: "852973890156",
      appId: "1:852973890156:web:c0343d48b8d44e342dc649"
    }
    setIsLoading(true)
    const app = initializeApp(firebaseConfig)
    const auth = getAuth(app)
    const provider = new GoogleAuthProvider()
    const result = await signInWithPopup(auth, provider)
    const token = await result.user.getIdToken()
    helper.axiosRequest({
      method: "POST",
      url: server.endpoints.google,
      data: { isToken: token }
    })
    .then(data => { utils.successLogin(data, login, navigate)  })
    .catch(error => { utils.error(error, setError) })
    .finally(() => { setIsLoading(false) })
  }

  const handleFlip = (event) => {
    event.preventDefault()
    setShowPassword(false)
    setShowPasswordConfirm(false)
    setFormData((prevData) => ({
      ...prevData,
      isFlipped: !prevData.isFlipped,
      name: "",
      lastName: "",
      username: "",
      password: "",
      passwordConfirm: "",
      email: "",
      gender: "",
      errors: {},
    }))
  }

  const handleKeyDown = (event, callback) => {
    if (event.key === "Enter") {
      event.preventDefault()
      callback(event)
    }
  }

  const handleTogglePasswordVisibility = () => {
    setShowPassword(prev => !prev)
  }

  const handleTogglePasswordVisibilityConfirm = () => {
    setShowPasswordConfirm(prev => !prev)
  }

  const state = {
    error,
    setError,
    formData,
    isLoading,
    success,
    setSuccess,
    warning,
    setWarning,
    showPassword,
    setShowPassword,
    showPasswordConfirm,
    setShowPasswordConfirm
  }

  const handlers = {
    handleInputChange,
    handleSubmit,
    handleSubmitRegister,
    handleFlip,
    handleKeyDown,
    handleTogglePasswordVisibility,
    handleTogglePasswordVisibilityConfirm,
    handleGoogle
  }

  const breakpoints = {
    isMobile
  }

  return { state, handlers, breakpoints }
}

export default useLogin
